@font-face {
  font-family: 'Barlow';
  src: url("../fonts/Barlow-Regular/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Barlow';
  src: url("../fonts/Barlow-Bold/Barlow-Bold.woff2") format("woff2"), url("../fonts/Barlow-Bold/Barlow-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow', sans-serif;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 60px 0;
  font-family: 'Barlow', sans-serif;
  overflow-y: auto;
}

.popup-prank__content {
  background-color: #323A46;
  max-width: 600px;
  width: 100%;
  border-radius: 15px;
}

.popup-prank__header {
  height: 140px;
  background-color: #3B4452;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.popup-prank__title {
  font-weight: 700;
  font-size: 30px;
  color: #fff;
  text-align: center;
}

.popup-prank__queue {
  color: #fff;
  font-size: 16px;
  text-align: center;
  margin-top: 12px;
}

.popup-prank__queue span {
  font-weight: 700;
  color: #2AC880;
}

.popup-prank__result {
  padding: 80px 0 40px 0;
  position: relative;
}

.popup-prank__result-img {
  position: absolute;
  top: -75px;
  left: 50%;
  margin-left: -75px;
  width: 150px;
  height: 150px;
}

.popup-prank__result-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.popup-prank__main {
  padding: 0 104px;
  height: 100%;
}

.popup-prank__block {
  padding: 30px 0;
}

.popup-prank__block + .popup-prank__block {
  border-top: 1px solid #3A4452;
}

.popup-prank__checkbox + .popup-prank__checkbox {
  margin-top: 10px;
}

.popup-prank__footer {
  border-top: 1px solid #3A4452;
  padding: 30px 104px;
}

.popup-prank__consent {
  padding: 10px 104px 40px 104px;
}

.popup-prank__summary {
  border-top: 1px solid #3A4452;
  padding: 30px 104px;
}

.popup-input + .popup-input {
  margin-top: 25px;
}

.popup-input__title {
  font-size: 14px;
  color: #Fff;
}

.popup-input__title span {
  color: #6D7883;
}

.popup-input__desc {
  font-size: 14px;
  color: #989CA2;
}

.popup-input__title + .popup-input__desc {
  margin-top: 8px;
}

.popup-input__title + .popup-input__item {
  margin-top: 12px;
}

.popup-input__desc + .popup-input__item {
  margin-top: 12px;
}

.popup-input__item {
  width: 100%;
  border-radius: 5px;
  background: #3B4452;
  border: 1px solid #3A4452;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 11px;
  transition: .3s border-color;
}

.popup-input__item:hover, .popup-input__item:focus {
  border-color: #6D7883;
}

.popup-input__input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
}

.popup-input__input:focus::placeholder {
  opacity: 0;
}

.popup-input__input::placeholder {
  color: #6D7883;
  opacity: 1;
  transition: .3s opacity;
}

.popup-input__textarea {
  height: 100px;
}

.popup-input__textarea textarea {
  resize: none;
}

.popup-tips__currency {
  display: flex;
  align-items: center;
}

.popup-tips__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.popup-tips__text {
  color: #fff;
  font-size: 14px;
}

.popup-tips__content {
  margin-top: 10px;
}

.popup-tips__footer {
  margin-top: 20px;
}

.popup-tips__noticy {
  font-size: 14px;
  line-height: 1.3;
  color: #2AC880;
}

.popup-summary__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.popup-summary__text {
  font-size: 16px;
  color: #fff;
}

.popup-summary__currency {
  display: flex;
  align-items: center;
}

.popup-currency__item {
  display: flex;
  align-items: center;
}

.popup-currency__item + .popup-currency__item {
  margin-left: 20px;
}

.popup-currency__img {
  flex-shrink: 0;
}

.popup-currency__text {
  color: #fff;
  font-size: 16px;
  margin-left: 10px;
  font-weight: 700;
}

.popup-submit__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
}

.popup-submit__btn + .popup-submit__btn {
  margin-left: 10px;
}

.popup-submit__btn {
  -webkit-appearance: none;
  width: 180px;
  height: 46px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2AC880;
  border: none;
  outline: none;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  transition: .3s background-color;
}

.popup-submit__btn:hover {
  cursor: pointer;
  background-color: #24A96D;
}

.popup-submit__btn_black {
  background-color: #2A303A;
  color: #fff;
}

.popup-submit__btn_black:hover {
  background-color: #111418;
}

.popup-submit__btn img {
  margin-left: 10px;
}

.popup-step__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-step__item + .popup-step__item {
  margin-left: 10px;
}

.popup-step__button {
  -webkit-appearance: none;
  width: 100px;
  border: none;
  background-color: transparent;
  color: #3B4452;
  font-size: 14px;
  padding-bottom: 5px;
  border-bottom: 3px solid #3B4452;
  transition: .3s color linear, .3s border-color linear;
}

.popup-step__button_current {
  border-color: #fff;
  color: #fff;
}

.popup-step__button_done {
  color: #2AC880;
  border-color: #2AC880;
}

.popup-step__button:hover {
  cursor: pointer;
}

.popup__close {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 20px;
  right: 20px;
}

.popup__close::before, .popup__close::after {
  content: '';
  position: absolute;
  top: 7.5px;
  height: 2px;
  width: 100%;
  display: block;
  background-color: #fff;
  transition: .3s background-color;
}

.popup__close::before {
  left: 0;
  transform: rotate(-45deg);
}

.popup__close::after {
  right: 0;
  transform: rotate(45deg);
}

.popup__close:hover {
  cursor: pointer;
}

.popup__close:hover::before, .popup__close:hover::after {
  background-color: #6D7883;
}

.radio-label:hover {
  cursor: pointer;
}

.radio-label__input {
  display: none;
}

.radio-label__input:checked + .radio-label__content .radio-label__text {
  color: #2AC880;
}

.radio-label__input:checked + .radio-label__content .radio-label__checkmark {
  border-color: #fff;
}

.radio-label__input:checked + .radio-label__content .radio-label__checkmark::before {
  opacity: 1;
}

.radio-label__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.radio-label__text {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  display: flex;
  align-items: center;
  transition: .3s color;
}

.radio-label__text-img {
  height: 25px;
  max-width: 32px;
  width: 100%;
  object-fit: contain;
  margin-right: 10px;
}

.radio-label__checkmark {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 1px solid #3A4452;
  position: relative;
  transition: .3s border-color;
  flex-shrink: 0;
}

.radio-label__checkmark:hover {
  border-color: #fff;
}

.radio-label__checkmark::before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  margin-top: -4px;
  left: 50%;
  margin-left: -4px;
  display: block;
  background-color: #2AC880;
  border-radius: 50%;
  opacity: 0;
  transition: .3s opacity;
}

.radio-label__text + .radio-label__checkmark {
  margin-left: 10px;
}

.radio-label__checkmark + .radio-label__text {
  margin-left: 10px;
}

.checkbox-currency + .checkbox-currency {
  margin-left: 20px;
}

.checkbox-consent + .checkbox-consent {
  margin-top: 15px;
}

.checkbox-block {
  background-color: #2A303A;
  border-radius: 5px;
}

.checkbox-block__header {
  border-bottom: 1px solid #3A4452;
  padding: 16px 15px;
}

.checkbox-block__content {
  padding: 10px 15px;
}

.checkbox-block__text {
  font-size: 14px;
  line-height: 18px;
  color: #fff;
}

.checkbox-label:hover {
  cursor: pointer;
}

.checkbox-label__input {
  display: none;
}

.checkbox-label__input:checked + .checkbox-label__content .checkbox-label__checkmark {
  border-color: #fff;
}

.checkbox-label__input:checked + .checkbox-label__content .checkbox-label__checkmark::before {
  opacity: 1;
}

.checkbox-label__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.checkbox-label__text {
  font-weight: 700;
  font-size: 14px;
  color: #2AC880;
  display: flex;
  align-items: center;
}

.checkbox-label__text-img {
  height: 25px;
  max-width: 32px;
  width: 100%;
  object-fit: contain;
  margin-right: 10px;
}

.checkbox-label__checkmark {
  width: 18px;
  height: 18px;
  border-radius: 5px;
  border: 1px solid #3A4452;
  position: relative;
  transition: .3s border-color;
  flex-shrink: 0;
}

.checkbox-label__checkmark:hover {
  border-color: #fff;
}

.checkbox-label__checkmark::before {
  content: '';
  position: absolute;
  width: 12px;
  height: 8px;
  top: 50%;
  margin-top: -4px;
  left: 50%;
  margin-left: -6px;
  display: block;
  background: url("../img/check.svg") center center no-repeat;
  background-size: contain;
  opacity: 0;
  transition: .3s opacity;
}

.checkbox-label__text + .checkbox-label__checkmark {
  margin-left: 10px;
}

.checkbox-label__checkmark + .checkbox-label__text {
  margin-left: 10px;
}

.checkbox-consent__text {
  color: #989CA2;
  font-size: 14px;
  font-weight: 400;
}

.checkbox-consent__text a {
  color: #fff;
  text-decoration: none;
  margin-left: 5px;
}

.checkbox-consent__content {
  justify-content: flex-start;
}

.tips-counter {
  text-align: center;
  font-size: 14px;
  color: #6D7883;
}

.tips-counter__content {
  width: 100%;
}

.tips-counter__minus, .tips-counter__plus {
  width: 46px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.tips-counter__minus:hover::before, .tips-counter__minus:hover::after, .tips-counter__plus:hover::before, .tips-counter__plus:hover::after {
  background-color: #24A96D;
}

.tips-counter__minus::before, .tips-counter__minus::after, .tips-counter__plus::before, .tips-counter__plus::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 16px;
  display: block;
  background-color: #2AC880;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -8px;
  transition: .3s background-color;
}

.tips-counter__minus:hover, .tips-counter__plus:hover {
  cursor: pointer;
}

.tips-counter__minus {
  border-right: 1px solid #323A46;
}

.tips-counter__minus::after {
  display: none;
}

.tips-counter__plus {
  border-left: 1px solid #323A46;
}

.tips-counter__plus::after {
  transform: rotate(90deg);
}

.tips-counter__control {
  height: 100%;
}

.tips-counter__content {
  max-width: 200px;
  margin: 0 auto;
}

.popup-input__item .react-tel-input .form-control {
  width: 100%;
  height: 100%;
  background-color: transparent;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
  padding-left: 70px;
}

.popup-input__item .react-tel-input .selected-flag:hover, .popup-input__item .react-tel-input .selected-flag:focus, .popup-input__item .react-tel-input .flag-dropdown.open .selected-flag, .popup-input__item .react-tel-input .flag-dropdown.open .selected-flag {
  background-color: transparent !important;
  background: transparent !important;
}

.popup-input__item .react-tel-input .flag-dropdown.open {
  background-color: transparent;
}

.popup-input__item .react-tel-input .flag-dropdown {
  background-color: transparent;
  border: none;
  border-right: 1px solid #323A46;
  padding-right: 15px;
}

.popup-input__item .react-tel-input .country-list .country {
  display: flex;
  align-items: center;
}

.popup-input__item .react-tel-input .country-list .country-name {
  color: #fff;
}

.popup-input__item .react-tel-input .country-list {
  background-color: #3B4452;
}

.popup-input__item .react-tel-input .country-list .country.highlight {
  background-color: #6D7883;
}

.popup-input__item .react-tel-input .country-list .country:hover {
  background-color: #6D7883;
}

.popup-input__item .react-tel-input .country-list .country .dial-code {
  color: #fff;
}

.popup-input__item .react-tel-input .selected-flag .arrow {
  background: url("arrow-country.svg") center center no-repeat;
  background-size: contain;
  width: 15px;
  height: 10px;
  border: none;
  top: 50%;
  margin-top: -5px;
  left: 25px;
  transition: .3s all linear;
}

.popup-input__item .react-tel-input .selected-flag .arrow.up {
  border: none;
  transform: rotate(180deg);
}

@media screen and (max-width: 768px) {
  .popup-prank {
    padding: 30px 15px;
  }
  .popup-prank__main {
    padding: 0 15px;
  }
  .popup-prank__consent {
    padding: 0 15px 30px 15px;
  }
  .popup-prank__footer, .popup-prank__summary {
    padding: 30px 15px;
  }
  .popup-step__button {
    width: 75px;
  }
}

@media screen and (max-width: 375px) {
  .popup__close {
    top: 10px;
    right: 10px;
  }
}
