* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow', sans-serif;
}


.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);

  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 60px 0;

  font-family: 'Barlow', sans-serif;
  overflow-y: auto;

  &-prank {
    &__content {
      background-color: #323A46;
      max-width: 600px;
      width: 100%;
      border-radius: 15px;
    }

    &__header {
      height: 140px;
      background-color: #3B4452;
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;

      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }

    &__title {
      font-weight: 700;
      font-size: 30px;
      color: #fff;
      text-align: center;
    }

    &__queue {
      color: #fff;
      font-size: 16px;
      text-align: center;
      margin-top: 12px;

      span {
        font-weight: 700;
        color: #2AC880;
      }
    }

    &__result{
      padding: 80px 0 40px 0;
      position: relative;
      &-img{
        position: absolute;
        top: -75px;
        left: 50%;
        margin-left: -75px;
        width: 150px;
        height: 150px;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    &__main {
      padding: 0 104px;
      height: 100%;
    }

    &__block {
      padding: 30px 0;
    }

    &__block + &__block {
      border-top: 1px solid #3A4452;
    }

    &__checkbox + &__checkbox {
      margin-top: 10px;
    }

    &__footer {
      border-top: 1px solid #3A4452;
      padding: 30px 104px;
    }

    &__consent {
      padding: 10px 104px 40px 104px;
    }

    &__summary {
      border-top: 1px solid #3A4452;
      padding: 30px 104px;
    }
  }

  &-input + &-input {
    margin-top: 25px;
  }

  &-input {
    &__title {
      font-size: 14px;
      color: #Fff;

      span {
        color: #6D7883
      }
    }

    &__desc {
      font-size: 14px;
      color: #989CA2;
    }

    &__title + &__desc{
      margin-top: 8px;
    }

    &__title + &__item{
      margin-top: 12px;
    }

    &__desc + &__item{
      margin-top: 12px;
    }

    &__item {
      width: 100%;
      border-radius: 5px;
      background: #3B4452;
      border: 1px solid #3A4452;
      height: 46px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 14px 11px;

      transition: .3s border-color;

      &:hover, &:focus {
        border-color: #6D7883;
      }
    }

    &__input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      background-color: transparent;
      color: #fff;
      font-size: 14px;

      &:focus::placeholder {
        opacity: 0;
      }

      &::placeholder {
        color: #6D7883;
        opacity: 1;
        transition: .3s opacity;
      }
    }

    &__textarea {
      height: 100px;

      textarea {
        resize: none;
      }
    }
  }

  &-tips {
    &__currency {
      display: flex;
      align-items: center;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__text {
      color: #fff;
      font-size: 14px;
    }

    &__content {
      margin-top: 10px;
    }

    &__footer {
      margin-top: 20px;
    }

    &__noticy {
      font-size: 14px;
      line-height: 1.3;
      color: #2AC880;
    }
  }

  &-summary {
    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__text {
      font-size: 16px;
      color: #fff;
    }

    &__currency {
      display: flex;
      align-items: center;
    }
  }

  &-currency {
    &__item {
      display: flex;
      align-items: center;
    }

    &__item + &__item {
      margin-left: 20px;
    }

    &__img {
      flex-shrink: 0;
    }

    &__text {
      color: #fff;
      font-size: 16px;
      margin-left: 10px;
      font-weight: 700;
    }
  }

  &-submit {
    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: center;

      margin-top: 25px;
    }

    &__btn + &__btn {
      margin-left: 10px;
    }

    &__btn {
      -webkit-appearance: none;
      width: 180px;
      height: 46px;
      border-radius: 5px;

      display: flex;
      align-items: center;
      justify-content: center;

      background-color: #2AC880;
      border: none;
      outline: none;

      color: #fff;
      font-size: 14px;
      font-weight: 700;

      transition: .3s background-color;

      &:hover {
        cursor: pointer;

        background-color: #24A96D;
      }

      &_black{
        background-color: #2A303A;
        color:#fff;

        &:hover{
          background-color: #111418;
        }
      }

      img {
        margin-left: 10px;
      }
    }
  }

  &-step{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__item + &__item{
      margin-left: 10px;
    }
    &__button{
      -webkit-appearance: none;

      width: 100px;

      border:none;
      background-color: transparent;
      color:#3B4452;
      font-size: 14px;
      padding-bottom: 5px;

      border-bottom: 3px solid #3B4452;

      transition: .3s color linear, .3s border-color linear;

      &_current{
        border-color: #fff;
        color:#fff;
      }

      &_done{
        color:#2AC880;
        border-color: #2AC880;
      }

      &:hover{
        cursor: pointer;
      }
    }
  }

  &__close {
    position: absolute;
    width: 15px;
    height: 15px;
    top: 20px;
    right: 20px;

    &::before, &::after {
      content: '';
      position: absolute;
      top: 7.5px;
      height: 2px;
      width: 100%;
      display: block;
      background-color: #fff;

      transition: .3s background-color;
    }

    &::before {
      left: 0;
      transform: rotate(-45deg);
    }

    &::after {
      right: 0;
      transform: rotate(45deg);
    }

    &:hover {
      cursor: pointer;

      &::before, &::after {
        background-color: #6D7883;
      }
    }
  }
}

.radio{
  &-label {
    &:hover {
      cursor: pointer;
    }

    &__input {
      display: none;

      &:checked + .radio-label__content {
        .radio-label__text{
          color:#2AC880;
        }
        .radio-label__checkmark {
          border-color: #fff;

          &::before {
            opacity: 1;
          }
        }
      }
    }

    &__content {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__text {
      font-weight: 700;
      font-size: 14px;
      color:#fff;
      display: flex;
      align-items: center;
      transition: .3s color;

      &-img {
        height: 25px;
        max-width: 32px;
        width: 100%;
        object-fit: contain;
        margin-right: 10px;
      }
    }

    &__checkmark {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 1px solid #3A4452;
      position: relative;
      transition: .3s border-color;
      flex-shrink: 0;

      &:hover {
        border-color: #fff;
      }

      &::before {
        content: '';
        position: absolute;
        width: 8px;
        height: 8px;
        top: 50%;
        margin-top: -4px;
        left: 50%;
        margin-left: -4px;
        display: block;
        background-color: #2AC880;
        border-radius: 50%;
        opacity: 0;
        transition: .3s opacity;
      }
    }

    &__text + &__checkmark {
      margin-left: 10px;
    }

    &__checkmark + &__text {
      margin-left: 10px;
    }
  }
}

.checkbox {
  &-currency + &-currency {
    margin-left: 20px;
  }

  &-consent + &-consent {
    margin-top: 15px;
  }

  &-block {
    background-color: #2A303A;
    border-radius: 5px;

    &__header {
      border-bottom: 1px solid #3A4452;
      padding: 16px 15px;
    }

    &__content {
      padding: 10px 15px;
    }

    &__text {
      font-size: 14px;
      line-height: 18px;
      color: #fff;
    }
  }

  &-label {
    &:hover {
      cursor: pointer;
    }

    &__input {
      display: none;

      &:checked + .checkbox-label__content {
        .checkbox-label__checkmark {
          border-color: #fff;

          &::before {
            opacity: 1;
          }
        }
      }
    }

    &__content {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__text {
      font-weight: 700;
      font-size: 14px;
      color: #2AC880;
      display: flex;
      align-items: center;

      &-img {
        height: 25px;
        max-width: 32px;
        width: 100%;
        object-fit: contain;
        margin-right: 10px;
      }
    }

    &__checkmark {
      width: 18px;
      height: 18px;
      border-radius: 5px;
      border: 1px solid #3A4452;
      position: relative;
      transition: .3s border-color;
      flex-shrink: 0;

      &:hover {
        border-color: #fff;
      }

      &::before {
        content: '';
        position: absolute;
        width: 12px;
        height: 8px;
        top: 50%;
        margin-top: -4px;
        left: 50%;
        margin-left: -6px;
        display: block;
        background: url("../img/check.svg") center center no-repeat;
        background-size: contain;
        opacity: 0;
        transition: .3s opacity;
      }
    }

    &__text + &__checkmark {
      margin-left: 10px;
    }

    &__checkmark + &__text {
      margin-left: 10px;
    }
  }

  &-consent {
    &__text {
      color: #989CA2;
      font-size: 14px;
      font-weight: 400;

      a {
        color: #fff;
        text-decoration: none;
        margin-left: 5px;
      }
    }

    &__content {
      justify-content: flex-start;
    }
  }
}

.tips {
  &-counter {
    text-align: center;
    font-size: 14px;
    color: #6D7883;

    &__content {
      width: 100%;
    }

    &__minus, &__plus {
      width: 46px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      position: relative;

      &:hover {
        &::before, &::after {
          background-color: #24A96D;
        }
      }

      &::before, &::after {
        content: '';
        position: absolute;
        height: 2px;
        width: 16px;
        display: block;
        background-color: #2AC880;
        top: 50%;
        margin-top: -1px;
        left: 50%;
        margin-left: -8px;

        transition: .3s background-color;
      }

      &:hover {
        cursor: pointer;
      }
    }

    &__minus {
      border-right: 1px solid #323A46;

      &::after {
        display: none;
      }
    }

    &__plus {
      border-left: 1px solid #323A46;

      &::after {
        transform: rotate(90deg);
      }
    }

    &__control {
      height: 100%;
    }

    &__content {
      max-width: 200px;
      margin: 0 auto;
    }
  }
}


.popup-input__item .react-tel-input .form-control {
  width: 100%;
  height: 100%;
  background-color: transparent;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
  padding-left: 70px;
}

.popup-input__item .react-tel-input .selected-flag:hover, .popup-input__item .react-tel-input .selected-flag:focus, .popup-input__item .react-tel-input .flag-dropdown.open .selected-flag, .popup-input__item .react-tel-input .flag-dropdown.open .selected-flag {
  background-color: transparent !important;
  background: transparent !important;
}

.popup-input__item .react-tel-input .flag-dropdown.open {
  background-color: transparent;
}

.popup-input__item .react-tel-input .flag-dropdown {
  background-color: transparent;
  border: none;
  border-right: 1px solid #323A46;
  padding-right: 15px;
}

.popup-input__item .react-tel-input .country-list .country {
  display: flex;
  align-items: center;
}

.popup-input__item .react-tel-input .country-list .country-name {
  color: #fff;
}

.popup-input__item .react-tel-input .country-list {
  background-color: #3B4452;
}

.popup-input__item .react-tel-input .country-list .country.highlight {
  background-color: #6D7883;
}

.popup-input__item .react-tel-input .country-list .country:hover {
  background-color: #6D7883;
}

.popup-input__item .react-tel-input .country-list .country .dial-code {
  color: #fff;
}

.popup-input__item .react-tel-input .selected-flag .arrow {
  background: url("arrow-country.svg") center center no-repeat;
  background-size: contain;
  width: 15px;
  height: 10px;
  border: none;
  top: 50%;
  margin-top: -5px;
  left: 25px;
  transition: .3s all linear;
}

.popup-input__item .react-tel-input .selected-flag .arrow.up {
  border: none;
  transform: rotate(180deg);
}

@media screen and (max-width: 768px) {
  .popup {
    &-prank {
      padding: 30px 15px;

      &__main {
        padding: 0 15px;
      }

      &__consent {
        padding: 0 15px 30px 15px;
      }

      &__footer, &__summary {
        padding: 30px 15px;
      }
    }
    &-step{
      &__button{
        width: 75px;
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .popup {
    &__close {
      top: 10px;
      right: 10px;
    }
  }
}